<!-- 分组柱线图表
 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>分组柱线图表</h3>
      <div id="container"></div>
    </div>
    <div class="box">
      <h3>分组柱线图表-自定义样式</h3>
      <div id="containerGs"></div>
    </div>
    <div class="box">
      <h3>分组柱线图表-显示多折线</h3>
      <div id="containerGz"></div>
    </div>
  </div>
</template>

<script>
import { DualAxes } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const uvBillData = [
      { time: '2019-03', value: 350, type: 'uv' },
      { time: '2019-04', value: 900, type: 'uv' },
      { time: '2019-05', value: 300, type: 'uv' },
      { time: '2019-06', value: 450, type: 'uv' },
      { time: '2019-07', value: 470, type: 'uv' },
      { time: '2019-03', value: 220, type: 'bill' },
      { time: '2019-04', value: 300, type: 'bill' },
      { time: '2019-05', value: 250, type: 'bill' },
      { time: '2019-06', value: 220, type: 'bill' },
      { time: '2019-07', value: 362, type: 'bill' }
    ]

    const transformData = [
      { time: '2019-03', count: 800 },
      { time: '2019-04', count: 600 },
      { time: '2019-05', count: 400 },
      { time: '2019-06', count: 380 },
      { time: '2019-07', count: 220 }
    ]

    const dualAxes = new DualAxes('container', {
      data: [uvBillData, transformData],
      xField: 'time',
      yField: ['value', 'count'],
      geometryOptions: [
        {
          geometry: 'column',
          isGroup: true,
          seriesField: 'type'
        },
        {
          geometry: 'line',
          lineStyle: {
            lineWidth: 2
          }
        }
      ]
    })

    dualAxes.render()

    this.containerGs()
    this.containerGz()
  },
  methods: {
    containerGs() {
      const uvBillData = [
        { time: '2019-03', value: 350, type: 'uv' },
        { time: '2019-04', value: 900, type: 'uv' },
        { time: '2019-05', value: 300, type: 'uv' },
        { time: '2019-06', value: 450, type: 'uv' },
        { time: '2019-07', value: 470, type: 'uv' },
        { time: '2019-03', value: 220, type: 'bill' },
        { time: '2019-04', value: 300, type: 'bill' },
        { time: '2019-05', value: 250, type: 'bill' },
        { time: '2019-06', value: 220, type: 'bill' },
        { time: '2019-07', value: 362, type: 'bill' }
      ]

      const transformData = [
        { time: '2019-03', count: 800 },
        { time: '2019-04', count: 600 },
        { time: '2019-05', count: 400 },
        { time: '2019-06', count: 380 },
        { time: '2019-07', count: 220 }
      ]

      const dualAxes = new DualAxes('containerGs', {
        data: [uvBillData, transformData],
        xField: 'time',
        yField: ['value', 'count'],
        geometryOptions: [
          {
            geometry: 'column',
            isGroup: true,
            seriesField: 'type',
            columnWidthRatio: 0.4,
            label: {},
            color: ['#5B8FF9', '#5D7092']
          },
          {
            geometry: 'line',
            color: '#5AD8A6'
          }
        ],
        legend: {
          custom: true,
          position: 'bottom',
          items: [
            {
              value: 'uv',
              name: 'uv',
              marker: { symbol: 'square', style: { fill: '#5B8FF9', r: 5 } }
            },
            {
              value: 'bill',
              name: '账单',
              marker: { symbol: 'square', style: { fill: '#5D7092', r: 5 } }
            },
            {
              value: 'count',
              name: '数值',
              marker: { symbol: 'square', style: { fill: '#5AD8A6', r: 5 } }
            }
          ]
        }
      })

      dualAxes.render()
    },
    containerGz() {
      const uvBillData = [
        { time: '2019-03', value: 350, type: 'uv' },
        { time: '2019-04', value: 900, type: 'uv' },
        { time: '2019-05', value: 300, type: 'uv' },
        { time: '2019-06', value: 450, type: 'uv' },
        { time: '2019-07', value: 470, type: 'uv' },
        { time: '2019-03', value: 220, type: 'bill' },
        { time: '2019-04', value: 300, type: 'bill' },
        { time: '2019-05', value: 250, type: 'bill' },
        { time: '2019-06', value: 220, type: 'bill' },
        { time: '2019-07', value: 362, type: 'bill' }
      ]

      const transformData = [
        { time: '2019-03', count: 800, name: 'a' },
        { time: '2019-04', count: 600, name: 'a' },
        { time: '2019-05', count: 400, name: 'a' },
        { time: '2019-06', count: 380, name: 'a' },
        { time: '2019-07', count: 220, name: 'a' },
        { time: '2019-03', count: 750, name: 'b' },
        { time: '2019-04', count: 650, name: 'b' },
        { time: '2019-05', count: 450, name: 'b' },
        { time: '2019-06', count: 400, name: 'b' },
        { time: '2019-07', count: 320, name: 'b' },
        { time: '2019-03', count: 900, name: 'c' },
        { time: '2019-04', count: 600, name: 'c' },
        { time: '2019-05', count: 450, name: 'c' },
        { time: '2019-06', count: 300, name: 'c' },
        { time: '2019-07', count: 200, name: 'c' }
      ]

      const dualAxes = new DualAxes('containerGz', {
        data: [uvBillData, transformData],
        xField: 'time',
        yField: ['value', 'count'],
        geometryOptions: [
          {
            geometry: 'column',
            isGroup: true,
            seriesField: 'type',
            columnWidthRatio: 0.4
          },
          {
            geometry: 'line',
            seriesField: 'name',
            lineStyle: ({ name }) => {
              if (name === 'a') {
                return {
                  lineDash: [1, 4],
                  opacity: 1
                }
              }
              return {
                opacity: 0.5
              }
            }
          }
        ]
      })

      dualAxes.render()
    }
  },
  created() {
    // var wrapperElement = '<div style="position:relative;"></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGs {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerpt {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGet {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerTexture {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



